        :key="i"
      >
        <div>
          <div class="diamond-preview">
            <img
              class="diamond-icon"
              :src="require('@/assets/icon/star-icon.png')"
            />
            <span class="diamond-count">{{ _.diamond }}</span>
          </div>
          <p class="bonus-diamond">
            bonus <span>{{ _.diffDiamond }}</span> diamond
          </p>
        </div>
        <div class="recharge-button" @click="diamondHandler(_)">
          <span class="diamond-money">₹{{ _.regionMoney }}</span>
          <img :src="require('@/assets/icon/right-icon.png')" />
        </div>
      </div>
    </div>
    <!-- <div class="title-tips">Recharge Plan</div>
    <div class="recharge-list">
     <div class="list-title">Minute basis:</div>
      <div class="list-items">
        <div
          class="item"
          @click="diamondHandler(_)"
          :class="{ hot: _.type === 1 }"
          v-for="(_, i) in products"
          :key="i"
        >
          <div class="label">
            <van-icon :name="require('@/assets/icon/star-icon.png')" />
            <div class="hot-label">{{ _.preDiamond+_.diffDiamond }}</div>
            <div>{{ _.diamond }}</div>
            <div class="hot-tag">HOT</div>
          </div>
          <div class="sale-money">{{ _.money }} USD</div>
        </div>
      </div>
    </div> -->

    <!-- <van-dialog
      v-model="dialog"
      @opened="renderCard"
      @confirm="purchase"
      title="Recharge"
      show-cancel-button
    >
      <div ref="card"></div>
    </van-dialog> -->
    <van-popup
      v-model="showRechargePopup"
      class="recharge-popup"
      closeable
      position="bottom"
    >
      <h2 class="pay-money">₹{{ curProduct.regionMoney }}</h2>
      <div class="pay-methods">
        <div
          class="pay-method"
          v-for="(method, index) in payMethods"
          :key="index"
        >
          <div class="left">
            <img class="pay-icon" :src="method.icon" />
            <h3>{{ method.name }}</h3>
          </div>
          <div
            class="right"
            @click="startPay(curProduct.productIosId, method.payParams)"
          >
            <span>Go Pay</span>
            <van-icon name="arrow" />
          </div>
        </div>
