<script>
import Vue from "vue";
import { NavBar, Popup } from "vant";
import * as Server from "@/server";
import { mapState, mapMutations } from "vuex";
// let stripe = window.Stripe("pk_test_kKiIIn5jbIixQ96SV4NpPZyf00hulVQYuC"),
//   elements = stripe.elements();
// let card = undefined;
// const styles = {
//   base: {
//     border: "1px solid #d8d8d8"
//   }
// };

Vue.use(NavBar)
  // .use(Dialog)
  .use(Popup);
export default {
  data() {
    return {
      dialog: false,
      products: [],
      curProduct: {},
      showUserInfo: false,
      showRechargePopup: false,
      rechargeUrl: "https://www.baidu.com",
      payMethods: [
        {
          payway: "9",
          name: "PAYTM",
          icon: "https://lichat.me/pay_way_paytm826.png",
          payParams: "paytm_in"
        },
        {
          payway: "11",
          name: "PhonePe",
          icon: "https://lichat.me/pay_way_phonepe1123.png",
          payParams: "wallet_in"
        },
        {
          payway: "8",
          name: "UPI",
          icon: "https://lichat.me/pay_way_upi0826.png",
          payParams: "upi_in"
        },
        {
          payway: "10",
          name: "EBanking",
          icon: "https://lichat.me/pay_way_ebanking826.png",
          payParams: "ebanking_in"
        }
      ]
    };
  },
  computed: {
    ...mapState(["profile"])
  },
  created() {
    if (this.$route.query.token) {
      localStorage.setItem("token", this.$route.query.token);
    }
    this.loadProfile();
    let url = window.location.href;
    if (/showInfo/.test(url)) this.showUserInfo = true;
    // this.showUserInfo = true;
  },
  methods: {
    ...mapMutations(["updateProfile"]),
    async loadProfile() {
      let { data, status } = await Server.Profile();
      if (status !== 0) {
        console.log("LoadProfile Failed => ", data);
        return;
      }
      this.updateProfile(data);
      localStorage.setItem("profile", JSON.stringify(data));
    },
    async loadData() {
      const { data, status } = await Server.ProductList();
      if (status === 0) {
        this.products = data;
      }
    },
    async diamondHandler(item) {
      console.log("Diamond handler > ", item);
      this.curProduct = item;
      this.showRechargePopup = true;
      // let { data, status } = await Server.PayByPaytm(item.productIosId);
      // if (status === 0) {
      //   window.location.href = data.payUrl;
      // }
      // console.log("Diamond Handler => ", data, status);
    },
    async startPay(productIosId, code) {
      let { data, status } = await Server.PayByUrl(productIosId, code);
      console.log(`StartPay => ${status} + }`, data);
      if (status !== 0) return;
      window.location.href = data.payUrl;
    },
    renderCard() {
      console.log("Stripe => RenderCard");
      // card.mount(this.$refs.card);
    },
    purchase() {
      console.log("Purchase");
      // stripe.createToken(card).then(async result => {
      //   if (result.error) {
      //     this.$toast(result.error.message);
      //   } else {
      //     console.log(result);
      //     const { data, status } = Server.PayByStripe({
      //       code: result.token.id,
      //       diamond: this.curProduct.diamond,
      //       money: this.curProduct.money,
      //       productId: this.curProduct.productId,
      //       type: this.curProduct.type
      //     });
      //     console.log(status, data);
      //   }
      // });
    },
    onClickLeft() {
      // this.$router.go(-1);
      this.$router.push("/home");
    }
  },
  mounted() {
    this.loadData();
    // card = elements.create("card", styles);
  }
};
</script>

<style lang="less">
iframe {
  border: none;
  width: 100%;
  height: 100%;
}
.left-bar {
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  color: #2626268a;
  cursor: pointer;
}
.van-dialog {
  width: 90%;
}
.content-block {
  padding: 20px;
  border-radius: 10px;
}
.content-block + .content-block {
  margin-top: 5px;
}
.available-coins,
