    rgb(255, 0, 122)
  );
  color: #fff;

  h1 {
    font-size: 20px;
    margin: 0;
  }

  h1 + * {
    margin-top: 15px;
  }

  .balance-container {
    display: flex;
    align-items: center;
    justify-content: center;

    .star-icon {
      max-height: 20px;
      margin-right: 5px;
    }

