  font-weight: 500;
  *:first-child {
    opacity: 0.7;
  }
}

.diamond-list {
  background: rgb(255, 230, 234);

  .diamond-item {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .diamond-item + .diamond-item {
    margin-top: 30px;
  }

  .diamond-preview {
    height: 20px;
    display: flex;
    align-items: flex-end;
  }
  .diamond-icon {
    max-height: 20px;
    margin-right: 5px;
  }
  .diamond-count {
    font-size: 18px;
    line-height: 20px;
    color: #000;
  }

  .diamond-money {
    color: #000;
    font-size: 18px;
    line-height: 20px;
  }

  .bonus-diamond {
    font-size: 14px;
    margin: 10px 0 0 0;

    span {
      color: #f00;
    }
  }

  .recharge-button {
    background: linear-gradient(to right, rgb(255, 0, 122), rgb(255, 1, 47));
