  max-height: 100vh;
  overflow-y: scroll;
  padding: 0px 20px 20px 20px;
  margin-bottom: 20px;
  color: #000;

  .balance-wrap {
    width: 315px;
    margin: 0 auto;
    height: 144px;
    background: linear-gradient(
      90deg,
      rgba(107, 125, 252, 1) 0%,
      rgba(244, 93, 130, 1) 100%
    );
    border-radius: 5px;
    position: relative;

    .title {
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
      color: #fff;
      position: absolute;
      top: 13px;
      left: 15px;
    }
    .value-wrap {
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translateX(-50%);
      .value {
        font-weight: bold;
        font-size: 36px;
        line-height: 43px;
        color: #fff;
      }
      .tips {
        font-size: 12px;
        color: #fff;
        line-height: 20px;
      }
    }
  }
  .title-tips {
    margin-top: 16px;
    font-size: 14px;
    color: #b5b5b5;
    line-height: 20px;
    padding: 16px 0;
  }
  .recharge-list {
    & + .recharge-list {
      margin-top: 38px;
    }
    .list-title {
      font-size: 14px;
      line-height: 20px;
    }
    .list-items {
      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 500;
        padding: 8px 0;

        .label {
          display: flex;
          align-items: center;
          .van-icon {
            margin-right: 5px;
          }
          .hot-label {
            font-size: 16px;
            // color: rgb(266,78,110);
            color: #000;
          }
          .hot-tag {
            background-color: #dd3d2e;
            color: #fff;
            border-radius: 5px;
            height: 18px;
