        }
      }
    }
  }
  //
  .recharge-popup {
    height: 250px;
    .pay-money {
      margin: 0;
      font-size: 18px;
      font-family: Robote;
      color: #262626;
      font-weight: 700;
      height: 50px;
      line-height: 50px;
      text-align: center;
    }

    .pay-methods {
      .pay-method {
        margin: 5px 15px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        transition: all 0.4s ease-in-out;
        &:hover {
          opacity: 0.95;
        }
        // border-bottom: 1px solid rgb(190, 185, 185);

        .left,
        .right {
          display: flex;
          align-items: center;

          img {
            max-width: 20px;
            margin-right: 5px;
          }

          h3 {
            margin: 0;
            font-size: 16px;
            color: #000;
            font-weight: 500;
          }

          span {
            font-size: 15px;
            color: #696969;
            font-weight: 400;
            margin-right: 5px;
          }

          .van-icon {
            color: #696969;
            transform: scale(0.6);
          }
        }
      }
    }
  }
